*{
    margin: 0px; padding: 0px; outline: 0px;
}
.con{
    width: 100%;
    height: 100vh;
    background: linear-gradient(to right, #434343 0%, black 100%);
    color: #ffff00;
    display: flex;
    justify-content: center;
    align-items: center;
}
.disp{
    font-size: 200px;
}

@media screen and (max-width: 1000px){
    .disp{
        font-size: 100px;
    }
}
@media screen and (max-width: 500px){
    .disp{
        font-size: 75px;
    }
}
